/* Styling for our main navbar */

/* Makes button fit to size of content */
.btn{
    width: -webkit-fit-content !important;
    width: -moz-fit-content !important;
    width: fit-content !important;
  }

.navbar{
  z-index: 1000;
}
/* Styling for our dropdown menu */

/* Main div component */
.menu{
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    display: inline-block;
}

/* Actual dropdown menu that displays */
.dropdown-menu{
    margin-top: -0.1rem;
    display: none;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding-left: 1rem;
    border-radius: 0;
}

/* Logic to make dropdown menu function */
.menu:hover .dropdown-menu, .menu:active .dropdown-menu {
    display: block;
}

/* Removes transition effect from button else it would affect how menu will look when toggling dark mode */
.btn{
    transition: none !important;
}
/* Styling for the main items on our homepage */

/* Main flex container that contains all our components */
.grid-flex-container{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    height: 90vh;
    padding-top: 70px;
}

/* Wrapper for our grid item */
.grid-wrapper{
    width: 20rem;
    margin: 1rem;
    height: auto;
    position: relative;
    z-index: 1;
}

/* Responsive image */
.grid-image{
    width: 100%;
    height: auto;
}

/* Dark overlay over our image */
.grid-overlay{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: #2a2829;
    opacity: 0.5;
    z-index: 2;
}

/* Text container */
.grid-text{
    color: #FFF;
    position: absolute;
    top: 45%;
    left: 0;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 100%;
    z-index: 3;
}

/* Actual text */
.grid-title{
    margin-top: 0;
    padding-top: 0;
    margin-bottom: 0;
    font-size: 1.64rem;
    text-align: center;
}

/* Logic below */

@media (min-width: 651px) {
    .grid-flex-container{
        flex-wrap: nowrap;
    }
}

@media (max-width: 651px) {
    .grid-title{
        font-size: 2rem;
    }
}

@media (min-width: 868px){
    .grid-title{
        font-size: 2.5rem;
    }
}

.grid-wrapper:hover .grid-overlay, .grid-wrapper:hover .grid-text{
    opacity: 0;
    transition: opacity ease 200ms;
}
/* Styling for the header images on certain pages */

/* Image */
.header{
    width: 100%;
    height: 100%;
    object-position: 50% 75%;
    object-fit: cover;
    box-sizing: border-box;
}

/* Dark overlay over our image */
.overlay{
    position: absolute;
    width: 100%;
    height: inherit;
    top: 0;
    left: 0;
    background-color: #2a2829;
    opacity: 0.2;
}

/* Box container for our header */
.header-box{
    height: 20rem;
}

/* Most outer container for margins */
.outer-box{
    margin-bottom: 4rem;
}

.page-title{
    text-align: center;
    margin-bottom: 3rem;
    font-size: 3.5rem;
}

.page-subtitle{
    text-align: center;
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    font-size: 2.5rem;
}

.page-caption{
    text-align: center;
    margin-top: 2.5rem;
    margin-bottom: 1.5rem;
    font-size: 1.08rem;
}
.figure{
    margin: 0.8rem;
    margin-bottom: 1rem;
}

.figure-caption{
    color: #04AEE7;
    font-size: 100%;
}

@media(max-width: 1199px){
    .figure-caption{
        font-size: 90%;
    }
}

@media(max-width: 476px){
    .figure{
        margin: 0.2rem;
    }
}
/* CSS for the skill info component */

/* Main container/card */
.skill-body{
    max-width: 40rem;
}

/* Whtite background is added for animations that have transparent backgrounds
    Also changes cursor to show animation can be clicked */
.animation{
    margin-bottom: 1rem;
    background-image: url(https://www.publicdomainpictures.net/pictures/30000/velka/plain-white-background.jpg);
    cursor: pointer;
}

.modal-animation{ 
    background-image: url(https://www.publicdomainpictures.net/pictures/30000/velka/plain-white-background.jpg);
    width: 100%;
    height: auto;
}

/* Skill icon */
.icon{
    margin-right: 0.25rem;
}

.skill-name{
    margin-left: 0.25rem;
    font-family: muli, sans-serif !important;
    font-weight: 700 !important;
    font-style: normal !important;
}

/* The elements beside the skill name */
.badge{
    margin-left: 0.25rem;
    font-family: acumin-pro, sans-serif !important;
}

.master-level{
    margin-bottom: 0.7rem;
    font-size: 0.95rem;
    font-weight: 300 !important;
}

/* The details below a description */
.skill-detail{
    margin-top: -0.5rem;
}

/* The description provided by API that has the numerical data */
.short-desc{
    font-weight: 300 !important;
}
/* Styling for class overview components in class overviews */

h1, h2, h3 ,h4 ,h5 ,h6{
    margin-top: 0.8rem;
    margin-bottom: 0.8rem;
}

/* Specific padding for Base Stats and Buffs tables */
.stat-td, .buff-td, .skill-td{
    padding: 0.3rem !important;
}

.class-title{
    margin-bottom: 2.5rem;
    font-size: 3.5rem;
}

.creation-title{
    margin-bottom: 2rem;
}

.more-info-link, .credit{
    margin: 1rem;
}

/* -----------------------WIDTH STYLING----------------------------- */
/* Specific image styling for images in How to Create component */
.creation-image{
    max-width: 100%;
    height: auto;
    margin: 0 0.5rem 0 0.5rem;
}

/* Max width for the base stats component */
.base-stats-width{
    max-width: 32rem;
}

/* Max width for the buffs & other actives component */
.buff-width{
    max-width: 37rem;
}

/* Pros and Cons component related */
.pros-cons-container{
    max-width: 36rem;
}

/* Max width for the how to create char component */
.extra-content-width{
    max-width: 50rem;
}


/* For iPad so that the elements do not display as blocks */
@media (max-width: 1199px){
    .property-container{
        max-width: 21rem;
    }

    .pros-cons-container{
        max-width: 33rem;
    }
}

/* Revert the changes made above for mobile users,
   above scruntches the class properties component a bit too much for mobile */
@media (max-width: 991px){
    .property-container{
        max-width: none;
    }

    .base-stat-title{
        font-size: 1.8rem;
    }
}
